<template>
  <div class="settings-page lc-custom-scroll">
    <div class="settings-page__doc-form">
      <el-alert v-if="error" :title="error" type="error" effect="dark" :closable="false" show-icon></el-alert>
      <h1>Settings for '{{title}}'</h1>
      <div class="settings-page__doc-form__group">
        <h2>Delete API</h2>
        <p>If you wish to delete this API, please click on the button below</p>
        <p>WARNING: This action cannot be undone</p>
        <el-button @click="deleteApi">Delete API Now</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import SettingsPage from './SettingsPage.hook'
const hook = new SettingsPage();
hook.setBackendNamespace("Pages/Editor").setElementName("SettingsPage");

export default {
  name: 'SettingsPage',
  beforeCreate() { hook.hook(this)},
  beforeDestroy(){ hook.clearToolBar();}, 
  mounted(){ hook.initToolbar(); },
  methods: hook.methods("deleteApi"),
  props: { },
  data() {
      return { 
        error: null
      }
  },
  computed: {
    title(){
      return this.openedDocument.editables["0"]?.properties["0"]?.title;
    },
    ...hook.getters("openedDocument")
  }

}

</script>

<style scoped lang="scss">

.settings-page {
  background-color: #1E1E1E;
  height:100%;
  width:100%;
  
  &__doc-form {
    h1{
      font-weight: 100;
      font-size:28px;
      margin-bottom:30px;
      cursor:text;
    }
    padding:80px;
    padding-top:40px;
    width:500px;
  }
}

</style>
