import {AtomicDesign} from '../../../../Frontend/Core/AtomicDesign';
import {getTopToolbarButtons} from '../../../../Frontend/Helpers/toolbar';

export default class SettingsPage extends AtomicDesign {
    
    initToolbar(){
        const toolbarButtons = getTopToolbarButtons("SAVE","FULLSCREEN");
        this.actions().setTopToolbar(toolbarButtons);
    }

    async deleteApi(){       
        try {
            const {value} = await this.vue.$prompt('Warning!!!. This action cannot be undone, Please enter the name of the API to proceed?', 'Delete API', { 
                confirmButtonText: 'OK',
                cancelButtonText: 'Cancel',
            });
            if (value === this.vue.title){
                const id = this.vue.openedDocument.id;
                const result = await this.backend().deleteDocument(id);
                const error = result?.error || result?.result?.error;

                if (!error){
                    this.vue.error = null;
                    this.vue.$router.push("/");
                }else {
                    this.vue.error = error;
                }
                
            }

        }catch (e){
            //cancel selected
        }
    }

}